<template>
 <div class="home">
   <div class="home__wrapper">
     <section class="home__media__wrapper">
       <div class="home__media">
         <transition  name="fadeIn">
           <img v-if="loaded" :src="imgUrl" class="home__media__image" alt=""/>
          <!-- <img v-if="image" :src="image" class="home__media__image" alt=""/> -->
         </transition>
       </div>
     </section>
    
      <section class="home__project">
          <router-link @mouseover="titleOver($event)" @mouseout="titleOut" @touchstart="titleOver($event)" @touchend="titleOut" ref="title" to="/decade-of-loud-music" class="home__project__title">Decade of Loud&nbsp;Music</router-link><br>
          <router-link @mouseover="titleOver($event)" @mouseout="titleOut" @touchstart="titleOver($event)" @touchend="titleOut" ref="title" to="/karpov-korchnoi" class="home__project__title">Karpov vs. Korchnoi</router-link><br>
          <router-link @mouseover="titleOver($event)" @mouseout="titleOut" @touchstart="titleOver($event)" @touchend="titleOut" ref="title" to="/near-earth-objects" class="home__project__title">Near Earth Objects</router-link><br>
          <router-link @mouseover="titleOver($event)" @mouseout="titleOut" @touchstart="titleOver($event)" @touchend="titleOut" ref="title" to="/ugc" class="home__project__title">Urban Green Council</router-link>
      </section>

   </div>
 </div>
</template>

<script>
import { gsap } from 'gsap'
import decade from '../assets/bottom.png'
import kvk from '../assets/KvK-theme-10.png'
import asteroids from '../assets/nasa-jpl-neo-1.png'
import ugc from '../assets/UGC-home.png'


export default {
  name: 'Home',
  data() {
    return {
     image:"",
     alt:"",
     showImg: true,
     imgUrl: "",
     loaded: false,
     images: [decade, kvk, asteroids, ugc], 
    };
  },

  mounted() {
    this.img = new Image();
    this.img.onload = () => {
      this.imgUrl = this.img.src;
      this.showImg = true
      this.img.src = "";
    }
    this.tl = gsap.timeline({paused: true})
    this.tl.to('.home__media', {
      border: 'black',
      ease: 'expo.out'
    })
  },
  methods: {
    titleOver(event) {
      const target = event.target || event.srcElement;
      this.loaded = true;
      if (target.innerHTML == "Decade of Loud&nbsp;Music") {
        this.img.src = this.images[0]
        this.alt = target.innerHTML
      }
      if (target.innerHTML == "Karpov vs. Korchnoi") {
        this.img.src = this.images[1]
        this.alt = target.innerHTML
      }
      if (target.innerHTML == "Near Earth Objects") {
         this.img.src = this.images[2]
        this.alt = target.innerHTML
      }
      if (target.innerHTML == "Urban Green Council") {
         this.img.src = this.images[3]
        this.alt = target.innerHTML
      }

      this.tl.play();      
    },
    titleOut() {
      this.tl.reverse();
      this.image = ""
      this.loaded = false;
    }
  
  }
}
</script>


