<template>
  <nav class="navigation">
    <!-- <div class="monogram__wrapper">
      <img class="monogram" :src="monogram"/>
    </div> -->
    <!-- <div class="monogram__wrapper">
      <transition  name="fadeIn">
        <img v-if="loaded == true" class="monogram" :src="monogram"/>
      </transition>
    </div> -->
    <ul class="navigation__list">
      <li class="navigation__list__item">
          <transition  name="fadeIn">
            <router-link to="/" class="navigation__list__link">James Troxel</router-link> 
          </transition>
      </li>
      <li class="navigation__list__item">
        <router-link to="/about" class="navigation__list__link"
          >About</router-link
        >
      </li>
      <li class="navigation__list__item">
        <router-link id="home" to="/" class="navigation__list__link"
          >&bull;
        </router-link>
      </li>
      <!-- <li class="navigation__list__item">
        <div class="navigation__list__link">&bull;</div>
      </li> -->
       <li
        class="navigation__list__item">
       <div class="navigation__spinner__wrapper">
          <p class="navigation__spinner"></p>
        <svg 
        @mouseover="traceLogo()"
        id="Layer_1" class="monogram" data-name="Layer 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 315 315">
        <path class="cls-1" fill="red" d="M107.5,107.5a100,100,0,0,0,200,0V7.5l-100,100v100a100,100,0,0,1-200,0Z" transform="translate(0 0)"/>
      <path class="cls-2" fill="none" stroke="white" stroke-linecap="round" stroke-linejoin="round" stroke-width="15px" d="M203.21,236.56A100,100,0,0,1,7.5,207.5l100-100a100,100,0,0,0,200,0V7.5l-100,100v70.94" transform="translate(0 0)"/>
</svg>
      
         <!-- <img class="monogram" src="../assets/JT_Mark-01.svg"/> -->
        <!-- <img v-if="loaded == true" class="monogram" :src="monogram"/> -->
        </div>
    </li>
    </ul>
  </nav>
</template>
<script>
import { gsap } from 'gsap'
import { TextPlugin } from "gsap/TextPlugin";
import GT from "../assets/monogram_GT-01.svg"
// import JWT from  "../assets/monogram_JWT-01.svg"
import Broken from  "../assets/monogram_BrokenType-01.svg"
import MIT from  "../assets/monogram_MIT-01.svg"
import Tongue from  "../assets/monogram_tongue-01.svg"
import HP from  "../assets/monogram_HP-01.svg"

gsap.registerPlugin(TextPlugin);
export default {
  name: "Navigation",
  data() {
    return {
      loaded: false,
      monogram: [],
      monograms: [GT, MIT, Broken, Tongue, HP],
      cyan: false
    };
  },
  watch:{
    $route (){
        this.traceLogo();
        this.loaded = true;
        if (this.$route.name == "Home"){
        this.tl.reverse(); 
      } else {
        this.tl.play();
        // return '\u2190 Home' 
        // + 'Home' + '\u2192'
      }
    }
  },
  updated() {},
  computed: {},
  created() {},
  mounted() {
    window.onload = [this.displayClock(), this.changeMonogram()];
    this.tl = gsap.timeline({paused: true})
    this.tl.to('#home', {
      text: '\u2190 Home',
      duration: 0.35,
      ease: 'expo.out'
    })
  },
  methods: {
    traceLogo(){
    this.mark = document.querySelector('.cls-1')
    this.path = document.querySelector('.cls-2')
    this.pathLength = this.path.getTotalLength()
    this.timeline = gsap.timeline({ paused: true, yoyo:true, repeat:1 })

    this.timeline.fromTo(this.path, {
      strokeDashoffset: this.pathLength,
      strokeDasharray: `${this.pathLength} ${this.pathLength}`
    }, {
      strokeDashoffset: 0,
      strokeDasharray: `${this.pathLength} ${this.pathLength}`
    })
     this.timeline.fromTo(this.mark, {
      autoAlpha: 1,
      duration: .75
    }, {
      autoAlpha: 0,
      duration: .75
    }, '-=1').play()
    },
    changeNav(){
      
      // console.log(this.$route.name)
      if (this.$route.name == "Home"){
        return '\u2022' 
      } else {
        this.tl.play();
        // return '\u2190 Home' 
        // + 'Home' + '\u2192'
      }
    },
    changeMonogram() {
      this.monogram = this.monograms[Math.floor(Math.random() * this.monograms.length)];
     this.loaded = !this.loaded;
    },
    displayClock() {
      // const display = new Date().toLocaleTimeString();
      const clock = document.querySelector(".navigation__spinner");
      clock.innerHTML = "FULL &bull; STACK &bull; DESIGNER &bull;";
      this.spinner = document.querySelector(".navigation__spinner");
      this.spinner.innerHTML = this.spinner.textContent.replace(
        /\S/g,
        "<span>$&</span>"
      );
      this.element = document.querySelectorAll(".navigation__spinner span");
      for (let i = 0; i < this.element.length; i++) {
        this.element[i].style.transform =
          "rotate(" + i * (360 / this.element.length) + "deg)";
      }
      setTimeout(this.displayClock, 1000);
    },
  },
};
</script>
